/* =====================================================================================
Fonts */
@font-face {
  font-family: "HaginCaps";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Oswald"), url("../fonts/HaginCapsMedium.woff2") format("woff2"), url("../fonts/HaginCapsMedium.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Oswald"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff"); }

/* =====================================================================================
Variables */
/* =====================================================================================
Basic styles */
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "HaginCaps", "Helvetica", sans-serif;
  font-size: 18px;
  line-height: 18px;
  font-weight: 400;
  color: #817155; }

img {
  max-width: 100%;
  height: auto; }

a {
  text-decoration: none; }

* {
  box-sizing: border-box; }

/* =====================================================================================
Container */
.container {
  max-width: 1366px;
  width: 100%;
  margin: 0 auto;
  padding-top: 0;
  padding-right: 42px;
  padding-bottom: 0;
  padding-left: 53px; }

/* =====================================================================================
Media max-width: 1150px*/
@media (max-width: 1150px) {
  .container {
    padding-right: 15px;
    padding-left: 15px; } }

/* =====================================================================================
Visually-hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* =====================================================================================
Body */
/* =====================================================================================
Header */
.header {
  position: fixed;
  top: 0;
  width: 100%;
  padding-top: 37px;
  color: #9b8869; }

.header__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center; }

.header__user-block {
  display: none;
  flex-basis: 100%;
  justify-content: flex-start; }

.header__link-name {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  font-family: "Roboto";
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.6px;
  color: #9b8869; }

.header__link-name svg {
  margin-right: 10px;
  fill: #9b8869; }

.header__link-logout svg {
  width: 16px;
  height: 18px;
  fill: #ba4f4f; }

.header__logo {
  display: block;
  width: 184px;
  height: 29px; }

/* =====================================================================================
Media max-width: 1060px*/
@media (max-width: 1150px) {
  .header__logo {
    flex-basis: 70%; }
  .header__user-block {
    display: none; }
  .body--nojs .header {
    position: static;
    background-color: #171515; }
  .body--nojs .header__user-block {
    display: flex;
    justify-content: space-between;
    align-items: center; }
  .body--nojs .header__logo {
    order: 2; } }

/* =====================================================================================
Hero */
.hero {
  min-height: 100vh;
  background-image: url("../img/hero-bg.jpg");
  background-repeat: no-repeat;
  background-size: cover; }

/* =====================================================================================
Main-nav */
.main-nav {
  max-width: 741px;
  width: 100%; }

.main-nav__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.main-nav__item a {
  font-size: 18px;
  line-height: 18px;
  letter-spacing: 0.7px;
  text-transform: uppercase;
  color: #9b8869; }

.main-nav__item a:focus,
.main-nav__item a:hover {
  color: #d89527; }

.main-nav__item a:active {
  color: #ff9e00; }

.main-nav__item--basket,
.main-nav__item--search {
  display: none; }

/* =====================================================================================
Media max-width: 1050px*/
@media (max-width: 1150px) {
  .main-nav {
    display: none;
    margin-top: 33px;
    order: 1; }
  .body--nojs .main-nav {
    display: block;
    margin-bottom: 20px; }
  .main-nav__list {
    flex-direction: column;
    padding-left: 15px; }
  .main-nav__item {
    max-width: 242px;
    margin-bottom: 20px; }
  .main-nav__item--basket,
  .main-nav__item--search {
    display: block; }
  .main-nav__item--basket {
    padding-top: 20px;
    padding-bottom: 20px;
    border-top: 1px solid #9b8869;
    border-bottom: 1px solid #9b8869; } }

/* =====================================================================================
User-nav */
.user-nav__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none; }

.user-nav__item a {
  display: block; }

.user-nav__item svg {
  fill: #9b8869; }

.user-nav__item--user {
  width: 18px;
  height: 21px; }

.user-nav__item--search {
  margin-left: 28px;
  width: 18px;
  height: 18px; }

.user-nav__item--basket {
  margin-left: 23px;
  width: 22px;
  height: 19px; }

.user-nav__item svg:focus,
.user-nav__item svg:hover {
  fill: #d89527; }

.user-nav__item svg:active {
  fill: #ff9e00; }

/* =====================================================================================
Media max-width: 1150px*/
@media (max-width: 1150px) {
  .user-nav {
    flex-basis: 30%; }
  .user-nav__list {
    justify-content: flex-end; }
  .user-nav__item--user {
    display: none; }
  .user-nav__item--search {
    display: none; }
  .user-nav__item--basket {
    margin-left: 0; }
  .body--nojs .user-nav {
    order: 3; } }
